import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { CusOperationHttpService } from "./../../../http/custom-operation/custom-operation.http.service";
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { tableMessage } from 'app/@uisftech/common/constant/config';


@Component({
  selector: 'app-custom-search-download',
  templateUrl: './custom-search-download.component.html',
  styleUrls: ['./custom-search-download.component.scss'],
  providers: [CusOperationHttpService, Commfunc]
})
export class CustomSearchDownloadComponent implements OnInit {
  tabMesg: any = tableMessage;//表格中无数据时显示的内容
  public fileId: string = '';
  public downloadSource: string = ''
  public fileName: string = '';
  public initiatedDate: string = ''
  public operateNo: string = ''
  public operateName: string = ''
  public msgs: any = [];
  public downloadSourceCode: any = [];
  queryParams:any;
  //获取所有码值
  code: any;
  dataTable: any = [];
  constructor(
    public httpService: CusOperationHttpService,
    public route: ActivatedRoute,
    public commfunc: Commfunc,
    private activeRoute: ActivatedRoute,
  ) {
    this.codeValues();
    this.downloadSourceCode = this.code['DownloadSourceCode']
  }

  ngOnInit() {
    this.activeRoute.queryParams.subscribe(params => {
      this.queryParams = JSON.parse(params.data);
      console.log(this.queryParams);
    });
    this.fileId = this.route.snapshot.params['fileId'],
      this.doQuery();
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  //审批-详情
  doQuery() {
    let params = {
      fileId: this.fileId
    }
    let ob = this.httpService.queryDownloadCenterApproveProcess(params);
    ob.subscribe(data => {
      if (data.returnCode.code == "success") {
        this.dataTable = data.returnList;
        this.dataTable[0].fileName = this.queryParams['fileName'];
        this.dataTable[0].queryCondition = this.queryParams['queryCondition'];
        this.dataTable[0].totalNum = this.queryParams['totalNum'];
        if(this.queryParams['queryRange'] == '1'){
          this.dataTable[0].queryRange = '全部客户';
        }else if(this.queryParams['queryRange'] == '2'){
          this.dataTable[0].queryRange = '下辖主联系客户';
        }else if(this.queryParams['queryRange'] == '3'){
          this.dataTable[0].queryRange = '下辖协储员客户';
        }
        console.log(this.dataTable);
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    })
  }
}
