import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { CusOperationHttpService } from "./../../../http/custom-operation/custom-operation.http.service";
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { tableMessage } from 'app/@uisftech/common/constant/config';


@Component({
  selector: 'app-list-download-approve',
  templateUrl: './list-download-approve.component.html',
  styleUrls: ['./list-download-approve.component.scss'],
  providers: [CusOperationHttpService, Commfunc]
})
export class ListDownloadApproveComponent implements OnInit {
  tabMesg: any = tableMessage;//表格中无数据时显示的内容
  public fileId: string = '';
  public downloadSource: string = ''
  public fileName: string = '';
  public initiatedDate: string = ''
  public operateNo: string = ''
  public operateName: string = ''
  public msgs: any = [];
  public downloadSourceCode: any = [];
  //获取所有码值
  code: any;
  dataTable: any = [];
  constructor(
    public httpService: CusOperationHttpService,
    public route: ActivatedRoute,
    public commfunc: Commfunc
  ) {
    this.codeValues();
    this.downloadSourceCode = this.code['DownloadSourceCode']
  }

  ngOnInit() {

    this.fileId = this.route.snapshot.params['fileId'],
      this.doQuery();
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  //审批-详情
  doQuery() {
    let params = {
      fileId: this.fileId
    }
    let ob = this.httpService.queryDownloadCenterApproveProcess(params);
    ob.subscribe(data => {
      if (data.returnCode.code == "success") {
        this.dataTable = data.returnList;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    })
  }
}
