import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { ListDownloadApproveComponent } from "app/pages/tzb/custom/custom-information/advanced-search/list-download-approve/list-download-approve.component";
import { CustomSearchDownloadComponent } from "app/pages/tzb/custom/custom-information/advanced-search/custom-search-download/custom-search-download.component";

export const routes: Routes = [
    {
        //客户名单下载
        path: 'a/b/:fileId',
        component: ListDownloadApproveComponent
    },
    {
        //搜客客户名单下载
        path: 'a/c/:fileId',
        component: CustomSearchDownloadComponent
    }
];

export const OtherCustListDownRouting: ModuleWithProviders = RouterModule.forChild(routes);
